.searchResult {
  display: flex;
  position: relative;
  margin: 20px;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  cursor: pointer;
  /* width: 100%; */
}

.searchResult:hover {
  opacity: 0.8;
}

.searchResult > img {
  height: 200px;
  width: 350px;
  border-radius: 10px;
  overflow: hidden;
}

.searchResult__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 20px;
}

.searchResult__button {
  position: absolute;
  top: 20px;
  right: 40px;
  padding: 10px;
  background-color: white;
  border-radius: 5px;
}
.searchResult__button:hover {
  background-color: black;
  color: white;
}

.searchResult__infoTop {
  width: 40vw;
}

.searchResult__infoTop > h3 {
  font-weight: 300;
  margin-top: 10px;
}

.searchResult__infoTop > p {
  margin-top: 10px;
  font-size: 13px;
  color: gray;
}

.searchResult__infoBottom {
  display: flex;
  justify-content: space-between;
  /* width: 100%; */
}

.searchResult__star {
  color: red;
}

.searchResult__stars {
  display: flex;
  align-items: center;
  margin: 10px;
  margin-left: 0px;
}
.searchResults__price {
}
@media screen and (max-width: 600px) {
  .searchResult__infoBottom {
    display: block;
  }
}
@media screen and (max-width: 800px) {
  .searchResult > img {
    height: 150;
    width: 180px;
  }
}
@media screen and (min-width: 600px) {
  .searchResults__price > p {
    text-align: right;
  }
  .searchResults__price {
    position: absolute;
    bottom: 20px;
    right: 30px;
  }
}
